"use client"

import { useEffect, useState } from "react"
import { useAuth } from "@/context/auth-context"
import { useRouter } from "next/navigation"
import { ProtectedLayout } from "@/components/layout/protected-layout"
import { Card } from "@/components/ui/card"
import { Alert } from "@/components/ui/alert"
import StatCard from "@/components/dashboard/stat-card"
import SalesChart from "@/components/dashboard/sales-chart"
import { Button } from "@/components/ui/button"
import { AdminLayout } from "@/components/layout/admin-layout"

interface DashboardStats {
  totalSales: number
  fuelInventory: number
  activePumps: number
  attendants: number
}

export default function DashboardPage() {
  const { isAuthenticated, token, user, logout, register } = useAuth()
  const router = useRouter()
  const [stats, setStats] = useState<DashboardStats>({
    totalSales: 0,
    fuelInventory: 0,
    activePumps: 0,
    attendants: 0,
  })
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [newUserName, setNewUserName] = useState("")
  const [newUserEmail, setNewUserEmail] = useState("")
  const [newUserPassword, setNewUserPassword] = useState("")
  const [registerError, setRegisterError] = useState("")
  const [registerLoading, setRegisterLoading] = useState(false)

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login")
      return
    }

    const fetchDashboardData = async () => {
      try {
        const headers = { Authorization: `Bearer ${token}` }

        const [salesRes, fuelRes, pumpRes, attendantRes] = await Promise.all([
          fetch("http://localhost:5000/api/sales", { headers }),
          fetch("http://localhost:5000/api/fuel", { headers }),
          fetch("http://localhost:5000/api/pumps", { headers }),
          fetch("http://localhost:5000/api/attendants", { headers }),
        ])

        const salesData = await salesRes.json()
        const fuelData = await fuelRes.json()
        const pumpData = await pumpRes.json()
        const attendantData = await attendantRes.json()

        setStats({
          totalSales: salesData.data?.length || 0,
          fuelInventory: fuelData.data?.reduce((sum: number, f: any) => sum + f.quantity, 0) || 0,
          activePumps: pumpData.data?.length || 0,
          attendants: attendantData.data?.length || 0,
        })
      } catch (err) {
        setError("Failed to fetch dashboard data")
      } finally {
        setLoading(false)
      }
    }

    fetchDashboardData()
  }, [isAuthenticated, router, token])

  // Admin register new user
  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault()
    setRegisterError("")
    setRegisterLoading(true)
    try {
      await register(newUserName, newUserEmail, newUserPassword)
      setNewUserName("")
      setNewUserEmail("")
      setNewUserPassword("")
      alert("User registered successfully!")
    } catch (err: any) {
      setRegisterError(err.message || "Registration failed")
    } finally {
      setRegisterLoading(false)
    }
  }

  return (
    <ProtectedLayout>
      <AdminLayout>
      <div className="p-8">
        <h1 className="text-3xl font-bold text-slate-900 mb-8">Dashboard</h1>

        {error && <Alert variant="destructive">{error}</Alert>}

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <StatCard title="Total Sales" value={stats.totalSales} icon="üí∞" />
          <StatCard title="Fuel Inventory (L)" value={stats.fuelInventory} icon="‚õΩ" />
          <StatCard title="Active Pumps" value={stats.activePumps} icon="üîå" />
          <StatCard title="Attendants" value={stats.attendants} icon="üë•" />
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-8">
          <Card className="lg:col-span-2">
            <h2 className="text-xl font-semibold text-slate-900 mb-4">Sales Overview</h2>
            <SalesChart />
          </Card>

          <Card>
            <h2 className="text-xl font-semibold text-slate-900 mb-4">Recent Alerts</h2>
            <div className="space-y-3">
              <Alert variant="success">Low inventory on Super 95</Alert>
              <Alert>Pump 3 maintenance due</Alert>
            </div>
          </Card>
        </div>

        {user?.role === "ADMIN" && (
          <Card className="mb-8">
            <h2 className="text-xl font-semibold text-slate-900 mb-4">Register New User</h2>
            {registerError && <Alert variant="destructive">{registerError}</Alert>}
            <form onSubmit={handleRegister} className="space-y-4">
              <input
                type="text"
                placeholder="Name"
                value={newUserName}
                onChange={(e) => setNewUserName(e.target.value)}
                required
                className="w-full px-4 py-2 border rounded"
              />
              <input
                type="email"
                placeholder="Email"
                value={newUserEmail}
                onChange={(e) => setNewUserEmail(e.target.value)}
                required
                className="w-full px-4 py-2 border rounded"
              />
              <input
                type="password"
                placeholder="Password"
                value={newUserPassword}
                onChange={(e) => setNewUserPassword(e.target.value)}
                required
                className="w-full px-4 py-2 border rounded"
              />
              <Button type="submit" disabled={registerLoading}>
                {registerLoading ? "Registering..." : "Register User"}
              </Button>
            </form>
          </Card>
        )}

        <Button onClick={logout} className="bg-red-600 text-white">
          Logout
        </Button>
      </div>
      </AdminLayout>
    </ProtectedLayout>
  )
}
